import React, { Component } from "react";
import Head from './Head'
import {Redirect} from 'react-router-dom'
import Teammembers from './Teammembers'
import Textarea from './Textarea'
import Out from "../style";
import { Button,Icon,Modal } from 'antd';
import Nav from "../Nav";
import Foot from "../Foot";
import {Link} from 'react-router-dom'
import store from '../../redux/redux'
import axios from 'axios'
const headtext =
  "10~15";
const text=["","/","/","","","",""]
const textid=["project_introduce","problem","framework","strength","tokenmodel","project_strategy","project_community"]
const inf=["slogan",
"1234",
"",
"",
"Token",
"",
"IDTelegram"
]
const data={
  
  project_introduce:"",
  problem:"",
  framework:"",
  strength:"",
  tokenmodel:"",
  project_strategy:"",
  project_community:""
}
let   member=[]
class S1index extends Component {
  state = {
    current: "01",
    num:store.getState(),
    key:1,
    visible: false
  };
  add=()=>{

    if(document.getElementsByClassName("timemembers").length>4){
      alert("5")
      return
    }
    store.dispatch({type:"ADD"})
    console.log(store.getState())
    this.setState({
      num:store.getState()
    })
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  }

  handleOk = (e) => {
    console.log(e);
    window.location.hash='#/step1'
    this.setState({
      visible: false,
    });
  }
  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }
  error(title) {
    const modal = Modal.error({
      title: title,
      okText:""
    });
    // setTimeout(() => modal.destroy(), 1000);
  }
next=()=>{
  member=[]
let ifnull=()=>{
  let ceo=document.getElementsByClassName("timemembers")
  for (let index = 0; index < ceo.length; index++) {
    let ceoname=ceo[index].querySelector("#ceoname").value
    let ceojob=ceo[index].querySelector("#ceojob").value
    let ceoinf=ceo[index].querySelector(".ceoinf").value
    if((ceoname==""||undefined)||(ceoinf==""||undefined)||(ceojob==""||undefined)){
      this.error("")
      return true
    }else{
      member.push({
        member_name:ceoname,
        member_position:ceojob,
        member_introduce:ceoinf
      })
    }
  }

console.log(member)
  for (const key in data) {
  if (data.hasOwnProperty(key)) {
    if(document.getElementById(key).value==""||undefined||null){
      this.error("")
        return true
    }else{
      data[key]=document.getElementById(key).value
    }
  }
}
}

if(ifnull()==true){
  return false
}
console.log(member)
axios.post("http://cm.hrjykj.com:8090/index/Project/HomeAddProjectInfo",{
  project_id:localStorage.project_id,
  token:localStorage.token,
  member:member,
  project_introduce:  data.project_introduce,
  problem:  data.problem,
  framework:  data.framework,
  strength:  data.strength,
  tokenmodel:  data.tokenmodel,
  project_strategy:  data.project_strategy,
  project_community:  data.project_community
})
.then(function(json){
  console.log(json)
  json.status=="200"?window.location.hash='#/step3':"";
})
.catch(function(err){
  console.log(err)
})


}



  render() {
    console.log(this.state)
    let element=[]
    let elementadd=[]
for (let index = 0; index < text.length; index++) {
  element.push(<Textarea id={textid[index]} red="true" inf={inf[index]} text={text[index]} key={index}></Textarea>)
}
for(let i=0; i<this.state.num;i++){
  if(i===0){
    elementadd.push(<Teammembers  key={i} text=""  ifdel={false} id={"id"+(i+1)}></Teammembers>)
  } 
    else{
    elementadd.push(<Teammembers  key={i} ifdel={true} id={"id"+(i+1)}></Teammembers>)

    }
        }
    return (
      <div>
      <Nav></Nav>
      <div style={{width:"100%",position:"absolute",top:"65px",bottom:"65px",overflow:"auto",minWidth:"1200px"}}>
       <div style={{width:"50%",minWidth:"1200px",margin:"0 auto"}}>
        <Head step={1} text={headtext}></Head>
        {elementadd}
        <Icon onClick={this.add}  style={{display:"block",width:"24px", margin:"20px auto 0",fontSize:"24px"}} type="plus-circle-o" />
        <div style={Out}>
        <h3 style={{fontWeight:"600",fontSize:"24px",marginTop:"15px"}}></h3>
         {element}
         
        </div>
        </div>
        <p style={{textAlign:"center"}}>
         <Button  onClick={this.showModal} style={{ margin: "30px 43px 0 0",fontSize:"16px",height:"40px",width:"100px",background:"#fff",color:"#000",border:"none"}} type="primary"></Button>
         
         <Button onClick={this.next} style={{ margin: "30px 0",fontSize:"16px",height:"40px",width:"100px"}} type="primary"></Button>
         
         </p>
      </div>
       
<Foot></Foot>
<div>
      <Modal
        title=""
        visible={this.state.visible}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        okText=""
        cancelText=""
        closable={false}
        width="430px"
      >
        <p style={{fontSize:"16px",fontWeight:"600"}}><Icon type="question-circle" /> ?</p>
        <p style={{color:"#000",opacity:"0.65"}}></p>
      </Modal>
    </div>
      </div>
    );
  }
}
export default S1index;



