import { createStore } from "redux";

/**
 *  reducer (state, action) => state 
 *  action  state  state
 *
 * state 
 *  Immutable.js 
 *  state 
 *
 *  `switch` (helper)
 * 
 */
//  step1={
//     name: "",
//     job: "",
//     mail: "",
//     phone: "",
//     wchat: "",
//     objectname: "",
//     companyname: "",
//     originator: "",
//     officialwebsite: "",
//     need: [],
//     whitebook: "",
//     referrer: "",
//     suggestjob: ""
//   },
function counter(state = 1,action) {
  switch (action.type) {
    case "ADD":
      return state + 1;
    case "DEL":
      return state - 1;
    default:
      return state;
  }
}


//  Redux store 
// API  { subscribe, dispatch, getState }
let store = createStore(counter);


// const reducer = function (state, action) {
//     // ...
//     return new_state;
//   };

// 
// store.subscribe(() =>
//   console.log(store.getState())
// );

//  state  dispatch  action
// action 

// 1
export default store;



